import React, { useRef, useEffect } from "react";
import gsap from "gsap";
import ScrollTrigger from "gsap/ScrollTrigger";

gsap.registerPlugin(ScrollTrigger);

const WorkMidThirdCopy = () => {
  const headThirdCopyRef = useRef(null);

  useEffect(() => {
    const spans = gsap.utils.toArray(".head span", headThirdCopyRef.current);
    spans.forEach((span) => {
      gsap.fromTo(
        span,
        { scale: 0.2 }, //   
        {
          scale: 1, //   
          scrollTrigger: {
            trigger: span,
            start: "top bottom", //      
            end: "center center", //      
            scrub: true, //       
            markers: false, //     
            ease: "elastic.out(1,0.3)",
          },
        }
      );
    });
  }, []);

  return (
    <div ref={headThirdCopyRef} className="third-copy">
      <p className="head">
        <span>  </span>
        <span>  </span>
      </p>
      <p className="sub">
        Seamless interactions bridging reality and the virtual world
      </p>
    </div>
  );
};

export default WorkMidThirdCopy;
