import React, { useRef, useEffect } from "react";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

gsap.registerPlugin(ScrollTrigger);

const WorkMidBox = () => {
  const midBoxImg = useRef(null);

  useEffect(() => {
    const element = midBoxImg.current;

    gsap.fromTo(
      element,
      {
        scale: 1.8, //  
      },
      {
        scale: 1.0, //  
        ease: "power1.out", // ease  : power1.out     
        scrollTrigger: {
          trigger: element,
          start: "top bottom", //     
          end: "bottom top", //     
          scrub: true, //       .
          // markers: true, //       
        },
      }
    );
  }, []);

  return (
    <>
      <div className="project-midbox">
        <div ref={midBoxImg} className="project-midbox-img"></div>
        <p>Routebase: Crafting Tomorrows Web.</p>
      </div>
    </>
  );
};

export default WorkMidBox;
