import React, { useRef, useEffect } from "react";
import gsap from "gsap";
import ScrollTrigger from "gsap/ScrollTrigger";

gsap.registerPlugin(ScrollTrigger);

const WorkMidCopy = () => {
  const headCopyRef = useRef(null);

  useEffect(() => {
    const spans = gsap.utils.toArray(".head span", headCopyRef.current);
    spans.forEach((span) => {
      gsap.fromTo(
        span,
        { scale: 0.2 }, //   
        {
          scale: 1, //   
          scrollTrigger: {
            trigger: span,
            start: "top bottom", //      
            end: "center center", //      
            scrub: true, //       
            markers: false, //     
            ease: "elastic.out(1,0.3)",
          },
        }
      );
    });
  }, []);

  return (
    <div ref={headCopyRef}>
      <div className="first-copy">
        <p className="head">
          <span> </span>
          <span> </span>
        </p>
        <p className="sub">Rules are concise, and technology is flexible</p>
      </div>
      <div className="second-copy">
        <p className="head">
          <span>  .</span>
          <span> </span>
        </p>
        <p className="sub">
          Push beyond the limits of technology. With Routebase, it's possible.
        </p>
      </div>
    </div>
  );
};

export default WorkMidCopy;
